@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap');

* {
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
}

.flex-row {
    width: 100%;
}

.pricing-widget-inner-container {
    width: 350px;
    height: 600px;
    background: #FFFFFF;
    border: 1px solid #BBB8B8;
    box-shadow: 3px 5px 1px rgba(0, 0, 0, 0.25);
    border-radius: 0px 0px 24px 24px;
    padding-bottom: 20px;
}

.pest-widget-banner {
    width: 100%;
}

.pricing-widget-content {
    padding: 15px 20px;
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    height: 80%;
}

.pricing-widget-header-group {
    display: flex;
    width: 100%;
    align-items: center;
    margin-bottom: 5px;
    justify-content: space-between;
}

.pricing-widget-header {
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
    font-size: 30px;
}

.pricing-widget-header-medium {
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
    font-size: 23px;
}

.pricing-widget-subheader {
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 16px;
}

.pricing-widget-heading-emoji {
    width: 50px;
    height: 50px;
}

.pricing-widget-benefits-list {
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #000000;
    margin: 0;
    padding: 0;
    list-style: none;
}

.pricing-widget-benefits-list li {
    display: flex;
}

.pricing-widget-content-full {
    padding: 40px 20px 2px 20px;
    height: 100%;
    display: flex;
    align-content: space-between
}

.pricing-widget-check {
    width: 40px;
    height: 40px;
    margin-right: 10px;
}

.pricing-widget-pricestart {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 700;
    font-size: 19px;
    line-height: 135.19%;
}

.pricing-widget-pricestart-enlarged {
    font-size: 28px;
}

.pricing-widget-header, .pricing-widget-pricestart, .pricing-widget-button {
    height: 6%;
}

.pricing-widget-benefits-list {
    margin-top: 30px;
    display: flex;
    justify-items: space-between;
    flex-wrap: wrap;
    height: 55%;
}

.pricing-widget-form {
    width: 100%;
    height: 60%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.pricing-widget-form-group {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 35%;
}

.pricing-widget-form-group input {
    width: 100%;
    height: 50px;
    background: #FFFFFF;
    border: 1px solid #8A8A8A;
    border-radius: 5px;
    padding: 0 10px;
    margin-bottom: 10px;
}

.pricing-widget-form-group label {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    margin-bottom: 5px;
    color: #464646;
}

.pricing-widget-button {
    background: #6A77D9;
    border-radius: 7px;
    color: #FFFFFF;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Poppins', sans-serif;
    font-size: 20px;
    width: 100%;
    cursor: pointer;
}

.pricing-widget-button:hover {
    background: #5A67C9;
}